body {
  background: #9999a1;
  color: #e6e6e9;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  padding: 16px;
  padding-bottom: 100px; }

.body-wrapper {
  height: 100vh; }

.main-siteWrapper {
  max-width: 900px;
  margin: 10px auto;
  padding: 30px 0 0 0;
  width: 100%; }

.main {
  max-width: 800px;
  margin: 20px auto;
  padding: 30px 0 0 0;
  width: 100%; }
  .main__wrapper {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 60vh; }
    .main__wrapper__image {
      display: block;
      max-width: 500px;
      padding: 15px 0 0 0;
      width: 100%; }

.header__title {
  font-size: 2.5rem;
  padding: 30px 0 0 0; }
  .header__title--subtitle {
    font-size: 1.25rem; }

.navigation {
  align-items: center;
  background: #66666e;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  left: 0;
  padding: 20px;
  position: fixed;
  top: 0;
  width: 100%; }
  .navigation__link {
    color: #f4f4f6;
    font-weight: bold;
    margin-left: 30px;
    text-decoration: none; }

.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  padding: 20px;
  background: #66666e;
  color: #f4f4f6;
  font-weight: bold;
  font-size: 0.6rem;
  text-align: center; }

.section {
  margin: 10px auto;
  max-width: 900px;
  width: 100%; }
  .section__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    justify-content: stretch;
    padding: 10px 32px; }
  .section__list-item {
    align-items: center;
    border: 3px solid white;
    display: flex;
    justify-content: center;
    min-height: 100px;
    padding: 5px; }
  .section__link {
    color: white;
    font-size: 1.15rem;
    font-weight: bold;
    text-decoration: none; }
  .section__title {
    padding: 0; }
  .section__list {
    column-count: 2; }
  .section__link {
    color: white;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 32px;
    text-decoration: none; }

.backlink-wrapper {
  background: #66666e;
  bottom: 60px;
  margin: 8px;
  padding: 8px;
  position: fixed;
  right: 20px; }
  .backlink-wrapper__link {
    color: white;
    font-weight: bold;
    font-size: 0.8rem;
    text-decoration: none; }

@media (max-width: 640px) {
  .section__list {
    grid-template-columns: repeat(2, 1fr); }
  .section__list-item {
    min-height: 50px; }
  .section__link {
    font-size: 0.85rem; }
  .backlink-wrapper__link {
    font-size: 0.6rem; }
  .header__title {
    padding: 32px 0 0 0;
    margin-bottom: 0;
    font-size: 1.5rem; }
  .header__title--subtitle {
    font-size: 0.8rem; }
  .main__image {
    padding: 0;
    max-width: 350px; }
  .section__title {
    font-size: 0.9rem; }
  .section__list {
    column-count: 1; } }

@media (max-width: 420px) {
  .section__list {
    grid-template-columns: 1fr; }
  .main__image {
    padding: 0; } }
